@import "../../utilities";

section#home {
  padding: 0px 20px;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: $silver;
  font-family: $font-josefin;
  overflow-x: hidden;
  // background: pink;

  .container {
    width: 100%;
    max-width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: left;

    .hero {
      height: 50%;
      width: 100%;
      margin: auto;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;

      &-intro {
        padding-top: 40px;
        padding-bottom: 30px;

        h1 {
          font-size: 44px;
          line-height: 44px;
          letter-spacing: 1.2px;
          position: relative;
          display: block;

          &::after {
            content: url("../../assets/paint-stroke-1.svg");
            height: 100px;
            width: 200%;
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            // transform: translate(-10%, 15%);
            opacity: 0;
            pointer-events: none;
            transition: opacity 0.2s ease-in-out;
            z-index: -1;
          }
        }

        h3 {
          font-size: 64px;
          line-height: 64px;
          padding-top: 1rem;

          span {
            color: $yellow;
          }
        }
      }

      &-details {
        div {
          font-size: 32px;
          display: block;
          line-height: 32px;
          justify-self: center;
          // flex: 1;

          span {
            line-height: 42px;
          }

          p {
            padding-top: 1rem;
            padding-right: 15px;
            font-size: 16px;
            line-height: 27px;
            letter-spacing: 1.25px;
            font-weight: 400;
          }
        }
      }
      .cta {
        width: 100%;
        padding: 10px 0px;

        button {
          display: block;
          width: 100%;
          font-size: 24px;
          outline: none;
          // margin: 0 auto;
          padding: 10px;
          margin-top: 20px;
          cursor: pointer;
          transition: all 0.15s linear;
        }

        .btn-outline {
          border: 2px solid $yellow;
          // border-width: 3px;
          background: transparent;
          color: $silver;

          &:hover,
          &:focus {
            background: $yellow;
            color: $black;
          }
        }

        .btn-solid {
          background: $silver;
          border: 2px solid $silver;
          color: $black;

          &:hover,
          &:focus {
            background: $black;
            color: $silver;
          }
        }
      }
    }
  }
}
